import axios from 'axios';

import store from '../store/store';
import { authRefreshToken, authLogout } from '../store/auth/actions';

const quiz = axios.create({
  baseURL: 'http://ec2-3-82-54-117.compute-1.amazonaws.com/api/v1/',
});

quiz.interceptors.request.use((req) => {
  const state = store.getState();
  if (state.auth.token) {
    req.headers = {
      ...req.headers,
      Authorization: `Bearer ${state.auth.token}`,
    };
  }

  return req;
});

quiz.interceptors.response.use(undefined, (error) => {
  const originalRequest = error.config;
  console.log(originalRequest)
  if ((error.response.status === 401 && !originalRequest._retry) && !(originalRequest.url === "/auth/token/refresh/" && error.response.status === 401)) {
    originalRequest._retry = true;

    const refreshToken = localStorage.getItem("refreshToken");
    return quiz
      .post('/auth/token/refresh/', {
        "refresh": refreshToken
      })
      .then((res) => {
        store.dispatch(authRefreshToken(res.data.access));
        originalRequest.headers['Authorization'] = 'Bearer ' + res.data.access;
        return axios(originalRequest);
      })
      .catch(err => {
        console.log(err);
      });
  } else if(error.response.status === 401 && !originalRequest._retry) {
    store.dispatch(authLogout());
  }

  return Promise.reject(error);
});

export default quiz;
