import styles from './Modal.module.scss';

interface ModalProps {
  type: 'success' | 'error';
  content: string;
}

const Modal = ({ type, content }: ModalProps) => {
  const ModalClassess = [styles.Modal__Content];
  if (type === 'success') {
    ModalClassess.push(styles.Modal_Success);
  } else if (type === 'error') {
    ModalClassess.push(styles.Modal_Error);
  }

  return (
    <div className={styles.Modal}>
      <div className={ModalClassess.join(' ')}>{content}</div>
    </div>
  );
};

export default Modal;
