import jwt_decode from 'jwt-decode';

import axios from "../../api/quiz";
import AppThunk from "../AppThunk";
import {
  AuthData,
  AUTH_LOGIN_START,
  AUTH_LOGIN_SUCCESS,
  AUTH_LOGIN_FAIL,
  AUTH_LOGOUT,
  AUTH_NEW_TOKEN,
  AUTH_NEW_USER_DATA,
  AuthActionTypes,
  User,
} from "./types";

export const authLoginStart = (): AuthActionTypes => {
  return {
    type: AUTH_LOGIN_START,
  };
};

export const authLoginSuccess = (
  userData: User,
  token: string,
  refreshToken: string,
  tokenExpiration: number
): AuthActionTypes => {
  localStorage.setItem("token", token);
  localStorage.setItem("refreshToken", refreshToken);
  localStorage.setItem("tokenExpiration", tokenExpiration.toString());
  localStorage.setItem("userData", JSON.stringify(userData));

  return {
    type: AUTH_LOGIN_SUCCESS,
    user: userData,
    token: token,
    refreshToken: refreshToken,
    tokenExpiration: tokenExpiration
  };
};

export const authLoginFail = (err: Error): AuthActionTypes => {
  return {
    type: AUTH_LOGIN_FAIL,
    error: err,
  };
};

export const authRefreshToken = (newToken: string): AuthActionTypes => {
  localStorage.setItem("token", newToken);
  const decodedToken: {exp: number} = jwt_decode(newToken);
  localStorage.setItem("tokenExpiration", decodedToken.exp.toString());
  return {
    type: AUTH_NEW_TOKEN,
    newToken: newToken,
    newTokenExpiration: decodedToken.exp
  }
}

export const updateUserData = (userData: User): AuthActionTypes => {
  const userInfo: User = JSON.parse(localStorage.getItem('userData') || '');
  const updatedUserInfo = { ...userInfo, ...userData };
  localStorage.setItem('userData', JSON.stringify(updatedUserInfo));
  return {
    type: AUTH_NEW_USER_DATA,
    userData: userData
  }
}

export const authLogin = (authData: AuthData): AppThunk => (dispatch) => {
  dispatch(authLoginStart());
  
  axios
    .post("/auth/login/", authData)
    .then(({ data }) => {
      const decodedToken: {exp: number} = jwt_decode(data.access_token);

      dispatch(
        authLoginSuccess(
          {
            id: data.user.pk,
            email: data.user.email,
            first_name: data.user.first_name,
            last_name: data.user.last_name,
            username: data.user.username,
          },
          data.access_token,
          data.refresh_token,
          decodedToken.exp
        )
      );
    })
    .catch((err) => {
      dispatch(authLoginFail(err));
    });
};

export const authLogout = (): AuthActionTypes => {
  localStorage.removeItem('token');
  localStorage.removeItem('userData');
  localStorage.removeItem('refreshToken');
  localStorage.removeItem('tokenExpiration');
  return {
      type: AUTH_LOGOUT
  };
};

export const authCheckState = (): AppThunk => (dispatch) => {
  const token = localStorage.getItem("token");
  const refreshToken = localStorage.getItem("refreshToken") ?? '';
  const tokenExpiration = localStorage.getItem("tokenExpiration") ?? '';
  const userJSON = localStorage.getItem("userData");
  let user;
  if(userJSON !== null) {
    user = JSON.parse(userJSON);
  } 
  
  if (!token || !user) {
    dispatch(authLogout());
  } else {
    const userData: User = {
      id: user.id,
      email: user.email,
      first_name: user.first_name,
      last_name: user.last_name,
      username: user.username,
    };
  
    dispatch(authLoginSuccess(userData, token, refreshToken, +tokenExpiration));
  }
};