import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { connect, ConnectedProps } from 'react-redux';

import { AppState } from '../../store/store';
import { updateUserData } from '../../store/auth/actions';
import styles from './Settings.module.scss';
import axios from '../../api/quiz';
import Panel from '../../components/Panel/Panel';
import Button from '../../components/Button/Button';
import Modal from '../../components/Modal/Modal';

const mapStateToProps = (state: AppState) => ({
  user: state.auth.user,
});

const mapDispatchToProps = (dispatch: any) => ({
  updateUserData: (userData: any) => dispatch(updateUserData(userData))
});

const connector = connect(mapStateToProps, mapDispatchToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;

const Settings = ({ user, updateUserData }: PropsFromRedux) => {
  const { register, errors, handleSubmit, setValue } = useForm({});
  const [settingsStatus, setSettingsStatus] = useState('initial');

  useEffect(() => {
    setValue('username', user.username);
    setValue('firstName', user.first_name);
    setValue('lastName', user.last_name);
    setValue('email', user.email);
  }, [user, setValue])
  
  const onSubmit = async (data: any, e: any) => {
    const newUserData = {
      ...(data.username !== user.username && { username: data.username }),
      ...(data.firstName !== user.first_name && { first_name: data.firstName }),
      ...(data.lastName !== user.last_name && { last_name: data.lastName }),
      ...(data.email !== user.email && { email: data.email }),
    };
    if (data.avatar) {
      console.log(data.avatar[0]);
      const formData = new FormData();
      formData.append('Avatar', data.avatar[0]);
      axios
        .patch(`/upload-avatar/${user.id}/`, formData)
        .then(() => {
          setSettingsStatus('success');
          setTimeout(() => setSettingsStatus('initial'), 5000);
        })
        .catch((err) => {
          setSettingsStatus('error');
          setTimeout(() => setSettingsStatus('initial'), 5000);
          console.log(err);
        });
    }
    if (Object.keys(newUserData).length > 0)
    {
      axios
        .patch(`/user/${user.id}/`, newUserData)
        .then(() => {
          updateUserData(newUserData);
          setSettingsStatus('success');
          setTimeout(() => setSettingsStatus('initial'), 5000);
        })
        .catch((err) => {
          setSettingsStatus('error');
          setTimeout(() => setSettingsStatus('initial'), 5000);
          console.log(err);
        });
    } 
  };

  return (
    <Panel>
      {settingsStatus === 'success' ? <Modal type="success" content="Settings saved!" /> : null }
      {settingsStatus === 'error' ? <Modal type="error" content="Something went wrong!" /> : null }
      <div className={styles.Settings}>
        <h1>Settings</h1>
        <form
          className={styles.Settings__Form}
          onSubmit={handleSubmit(onSubmit)}
          noValidate
        >
          {/* USERNAME */}
          <div className={styles.Settings__Form__Input}>
            <input
              id="username"
              name="username"
              type="text"
              placeholder="Username"
              autoComplete="username"
              ref={register({
                required: 'This is required',
                minLength: {
                  value: 5,
                  message: 'Minimum 5 characters',
                },
              })}
            />
            <label htmlFor="username">Username</label>
            <div
              className={
                styles.Settings__ErrorMessage +
                ' ' +
                (errors['username'] && styles.Settings__ErrorMessage_show)
              }
            >
              {errors['username'] && errors['username'].message}
            </div>
          </div>
          {/* FIRST NAME */}
          <div className={styles.Settings__Form__Input}>
            <input
              id="firstName"
              name="firstName"
              type="text"
              placeholder="First name"
              autoComplete="given-name"
              ref={register({
                maxLength: {
                  value: 150,
                  message: 'Maximum 150 characters',
                },
              })}
            />
            <label htmlFor="firstName">First name</label>
            <div
              className={
                styles.Settings__ErrorMessage +
                ' ' +
                (errors['firstName'] && styles.Settings__ErrorMessage_show)
              }
            >
              {errors['firstName'] && errors['firstName'].message}
            </div>
          </div>
          {/* LAST NAME */}
          <div className={styles.Settings__Form__Input}>
            <input
              id="lastName"
              name="lastName"
              type="text"
              placeholder="Last name"
              autoComplete="family-name"
              ref={register({
                maxLength: {
                  value: 150,
                  message: 'Maximum 150 characters',
                },
              })}
            />
            <label htmlFor="lastName">Last name</label>
            <div
              className={
                styles.Settings__ErrorMessage +
                ' ' +
                (errors['lastName'] && styles.Settings__ErrorMessage_show)
              }
            >
              {errors['lastName'] && errors['lastName'].message}
            </div>
          </div>
          {/* EMAIL */}
          <div className={styles.Settings__Form__Input}>
            <input
              id="email"
              name="email"
              type="email"
              placeholder="E-mail"
              autoComplete="email"
              ref={register({
                minLength: {
                  value: 1,
                  message: 'Minimum 1 character',
                },
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: "Invalid email address",
                },
              })}
            />
            <label htmlFor="email">E-mail</label>
            <div
              className={
                styles.Settings__ErrorMessage +
                ' ' +
                (errors['email'] && styles.Settings__ErrorMessage_show)
              }
            >
              {errors['email'] && errors['email'].message}
            </div>
          </div>
          {/* AVATAR */}
          <div className={styles.Settings__Form__Input}>
            <input
              id="avatar"
              name="avatar"
              type="file"
              placeholder=""
              autoComplete="avatar"
              ref={register}
            />
            <label htmlFor="avatar">Avatar</label>
            <div
              className={
                styles.Settings__ErrorMessage +
                ' ' +
                (errors['avatar'] && styles.Settings__ErrorMessage_show)
              }
            >
              {errors['avatar'] && errors['avatar'].message}
            </div>
          </div>
          <div className={styles.Settings__Button}>
            <Button type="filled" isSubmit>Save</Button>
          </div>
        </form>
      </div>
    </Panel>
  );
};

export default connector(Settings);
