import { useEffect, useState } from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import axios from "../../api/quiz";

import styles from "./Matches.module.scss";
import Panel from "../../components/Panel/Panel";
import Loader from "../../components/Loader/Loader";
import Table from "../../components/Table/Table";
import Button from "../../components/Button/Button";
import { connect, ConnectedProps } from "react-redux";
import { AppState } from "../../store/store";


const mapStateToProps = (state: AppState) => ({
  UserId: state.auth.user.id,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;

type MatchesProps = PropsFromRedux & RouteComponentProps;

const Matches: React.FC<MatchesProps> = ({ history, UserId }) => {
  const [matchesList, setMatchesList] = useState([]);
  const tableTitles = ["Match Id", "Tournament Id", "Date", "Status"];

  useEffect(() => {
    axios
      .get("/match/")
      .then(({ data }) => {
        setMatchesList(data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const checkMatchDetails = (matchId: number) => {
    history.push(`/matches/${matchId}`);
  };

  let matchesElements = null;
  if (matchesList.length > 0) {
    matchesElements = matchesList.map((match: any) => {
      const date = new Date(match.CreatedDate);
      const newDate = date.toLocaleString(undefined, { hour12: false });

      return (
        <tr key={match.Id} onClick={() => checkMatchDetails(+match.Id)}>
          <td>{match.Id}</td>
          <td>{match.TournamentId || "(no tournament)"}</td>
          <td>{newDate}</td>
          <td>{match.Status}</td>
        </tr>
      );
    });
  }

  const handleCreateMatch = () => {
    axios.post('/match/', {UserId: [UserId], Questions: []})
    .then(({ data }) => {
      history.push(`/matches/${data.Id}`);
    })
    .catch(err => {
      console.log(err);
    })
  };

  return (
    <Panel>
      <div className={styles.Matches}>
        {matchesList.length > 0 ? (
          <>
            <h1>Matches</h1>
            <div className={styles.Matches__List}>
              <Table titles={tableTitles}>{matchesElements}</Table>
            </div>
            <div className={styles.Matches__Button}>
              <Button type="filled" handleClicked={handleCreateMatch}>Create match</Button>
            </div>
          </>
        ) : (
          <Loader />
        )}
      </div>
    </Panel>
  );
};

export default withRouter(connector(Matches));
