import { useEffect, useState } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import axios from '../../../api/quiz';
import styles from './MatchDetail.module.scss';
import Panel from '../../../components/Panel/Panel';
import Loader from '../../../components/Loader/Loader';
import Table from '../../../components/Table/Table';
import Button from '../../../components/Button/Button';
import DraggableRow from './DraggableRow/DraggableRow';

interface MatchDetailProps {
  match: {
    params: {
      matchId: number;
    };
  };
}

interface matchType {
  CreatedDate?: string;
  EndedDate?: string | null;
  Id?: number;
  Questions: any[];
  Status?: 'Created' | 'Locked' | 'Ended';
  TournamentId?: number;
  UserId: userType[];
}

interface userType {
  username: string;
  Id: number;
  Avatar: string;
  first_name: string;
  last_name: string;
  email: string;
  is_staff: boolean;
  is_active: boolean;
  date_joined: string;
  ranking_points: number | null;
}

type editModeType = 'none' | 'users' | 'questions';

const portalTable: HTMLElement = document.createElement('table');
const portal: HTMLElement = document.createElement('tbody');
portalTable.appendChild(portal);
portal?.classList.add('my-super-cool-portal');

if (!document.body) {
  throw new Error('body not ready for portal creation!');
}

document.body.appendChild(portal);

const MatchDetail = (props: MatchDetailProps) => {
  const [match, setMatch] = useState<matchType | null>(null);
  const [users, setUsers] = useState<userType[]>([]);
  const [questions, setQuestions] = useState<any[]>([]);
  const [editMode, setEditMode] = useState<editModeType>('none');

  useEffect(() => {
    let fetchedUsers: any;
    let fetchedQuestions: any;
    let matchDetails: any;

    axios
      .get(`/match/${props.match.params.matchId}/`)
      .then(({ data }) => {
        matchDetails = data;
        fetchedUsers = matchDetails.UserId.map(((fUser: any) => ({ Id: fUser })));
        const userRequests: any[] = [];
        fetchedUsers.forEach((fUser: any) => {
          userRequests.push(axios.get(`/user/${fUser.Id}/`));
        });
        return Promise.all(userRequests);
      })
      .then(response => {
        fetchedUsers = fetchedUsers.map((userEl: any) => {
          const userData = response.filter((usData: any) => usData.data.Id === userEl.Id)[0].data;
          return {...userData};
        })
        matchDetails.UserId = fetchedUsers;
        
        fetchedQuestions = matchDetails.Questions.map(((fQuestion: any) => ({ Id: fQuestion })));
        const questionRequests: any[] = [];
        fetchedQuestions.forEach((fQuestion: any) => {
          questionRequests.push(axios.get(`/question/${fQuestion.Id}/`));
        });
        return Promise.all(questionRequests);
      }).then((response) => {
        fetchedQuestions = fetchedQuestions.map((queEl: any) => {
          const questionData = response.filter((queData: any) => queData.data.Id === queEl.Id)[0].data;
          return {...questionData};
        });
        matchDetails.Questions = fetchedQuestions;
        setMatch(matchDetails);

        return axios.get('/user/');
      })
      .then(({ data }) => {
        const filtUsers = data.results.filter((filtUser: any) => {
          return !matchDetails.UserId.find((matchUs: any) => matchUs.Id === filtUser.Id);
        });
        setUsers(filtUsers);

        return axios.get('/question/');
      })
      .then(({ data }) => {
        const filtQuestions = data.results.filter((filtQuestion: any) => {
          return !matchDetails.Questions.find((matchUs: any) => matchUs.Id === filtQuestion.Id);
        });
        setQuestions(filtQuestions);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const toggleEditMode = (mode: editModeType) => {
    if (editMode === mode) {
      setEditMode('none');
    } else {
      setEditMode(mode);
    }
  };

  const onDragEnd = (result: any) => {
    if (!result.destination) {
      return;
    }

    if (result.destination.index === result.source.index) {
      return;
    }

    if (result.destination.droppableId === result.source.droppableId) {
      return;
    }

    if (!(result.destination.droppableId.includes("Users") && result.source.droppableId.includes("Users")) &&
        !(result.destination.droppableId.includes("Questions") && result.source.droppableId.includes("Questions"))) {
      return;
    }

    if (result.destination.droppableId === 'allUsers') { // remove user from match
      const newUsers: any = [...users];
      const newMatch: any = {...match};
      let newMatchUsers: any = [...newMatch.UserId];
      const foundUser: any = newMatchUsers.find(((usData: any) => usData.username === result.draggableId));
      
      newMatchUsers = newMatchUsers.filter((usData: any) => usData.username !== result.draggableId);
      newUsers.push(foundUser);
      
      setMatch({ ...newMatch, UserId: [...newMatchUsers] });
      setUsers([...newUsers]);

      const newUserList = newMatchUsers.map((userEl: any) => userEl.Id);
      axios.patch(`/match/${props.match.params.matchId}/`, {'UserId': newUserList})

    } else if (result.destination.droppableId === 'matchUsers') { // add user to match
      let newUsers: any = [...users];
      const newMatch: any = {...match};
      let newMatchUsers: any = [...newMatch.UserId];
      const foundUser: any = newUsers.find(((usData: any) => usData.username === result.draggableId));
      
      newUsers = newUsers.filter((usData: any) => usData.username !== result.draggableId);
      newMatchUsers.push(foundUser);
      
      setMatch({ ...newMatch, UserId: [...newMatchUsers] });
      setUsers([...newUsers]);

      const newUserList = newMatchUsers.map((userEl: any) => userEl.Id);
      axios.patch(`/match/${props.match.params.matchId}/`, {'UserId': newUserList})
      
    } else if (result.destination.droppableId === 'allQuestions') {
      const newQuestions: any = [...questions];
      const newMatch: any = {...match};
      let newMatchQuestions: any = [...newMatch.Questions];
      const foundQuestion: any = newMatchQuestions.find(((queData: any) => queData.Text === result.draggableId));
      
      newMatchQuestions = newMatchQuestions.filter((queData: any) => queData.Text !== result.draggableId);
      newQuestions.push(foundQuestion);
      
      setMatch({ ...newMatch, Questions: [...newMatchQuestions] });
      setQuestions([...newQuestions]);

      const newQuestionList = newMatchQuestions.map((queEl: any) => queEl.Id);
      axios.patch(`/match/${props.match.params.matchId}/`, {'Questions': newQuestionList})

    } else if (result.destination.droppableId === 'matchQuestions') {
      let newQuestions: any = [...questions];
      const newMatch: any = {...match};
      let newMatchQuestions: any = [...newMatch.Questions];
      const foundQuestion: any = newQuestions.find(((queData: any) => queData.Text === result.draggableId));
      
      newQuestions = newQuestions.filter((queData: any) => queData.Text !== result.draggableId);
      newMatchQuestions.push(foundQuestion);
      
      setMatch({ ...newMatch, Questions: [...newMatchQuestions] });
      setQuestions([...newQuestions]);

      const newQuestionList = newMatchQuestions.map((queEl: any) => queEl.Id);
      axios.patch(`/match/${props.match.params.matchId}/`, {'Questions': newQuestionList})
    }
  }

  const handleStartMatch = () => {
    axios
      .patch(`/match/${props.match.params.matchId}/`, { Status: 'Locked' })
      .then(() => {
        console.log('great')
      })
      .catch(err => {
        console.log(err);
      })
  }

  let matchContent = <Loader />;
  if (match) {
    switch (match.Status) {
      case 'Created':
        matchContent = (
          <>
            {editMode === 'users' ? (
              <Panel>
                <div className={styles.Created__Edit__Users}>
                  <h1>Users</h1>
                  <Droppable droppableId="allUsers">
                    {(droppableProvided) => (
                      <div ref={droppableProvided.innerRef} {...droppableProvided.droppableProps}>
                        <Table titles={['Id', 'Username']}>
                          {users.map((userEl, index) => (
                            <Draggable
                              draggableId={userEl.username}
                              index={index}
                              key={userEl.Id}
                            >
                              {(draggableProvided, draggableSnapshot) => (
                                <DraggableRow provided={draggableProvided}
                                snapshot={draggableSnapshot} portal={portal} content={{id: userEl.Id, username: userEl.username}}/>
                              )}
                            </Draggable>
                          ))}
                          {droppableProvided.placeholder}
                        </Table>
                      </div>
                    )}
                  </Droppable>
                </div>
              </Panel>
            ) : null}
            <Panel>
              <div className={styles.MatchDetail}>
                <h1>Match nr {props.match.params.matchId}</h1>
                <div className={styles.Created}>
                  <div className={styles.Created__Panels}>
                    <div className={styles.Created__Users}>
                      <h3>Users</h3>
                      <Droppable droppableId="matchUsers">
                      {(droppableProvided) => (
                          <div ref={droppableProvided.innerRef} {...droppableProvided.droppableProps} style={{width: '100%'}}>
                            <Table titles={['Id', 'Username']}>
                              {match.UserId.map((userEl, index) => (
                                <Draggable
                                draggableId={userEl.username}
                                index={index}
                                key={userEl.Id}
                              >
                                {(draggableProvided, draggableSnapshot) => (
                                  <DraggableRow provided={draggableProvided}
                                  snapshot={draggableSnapshot} portal={portal} content={{id: userEl.Id, username: userEl.username}}/>
                                )}
                              </Draggable>
                              ))}
                              {droppableProvided.placeholder}
                            </Table>
                          </div>
                        )}
                      </Droppable>
                      <div className={styles.Created__Button}>
                        <Button
                          type="filled"
                          handleClicked={() => toggleEditMode('users')}
                        >
                          Edit users
                        </Button>
                      </div>
                    </div>
                    <div className={styles.Created__Questions}>
                      <h3>Questions</h3>

                      <Droppable droppableId="matchQuestions">
                      {(droppableProvided) => (
                          <div ref={droppableProvided.innerRef} {...droppableProvided.droppableProps} style={{width: '100%'}}>
                            <Table titles={['Id', 'Question']}>
                              {match.Questions.map((que, index) => (
                                <Draggable
                                draggableId={que.Text}
                                index={index}
                                key={que.Id}
                              >
                                {(draggableProvided, draggableSnapshot) => (
                                  <DraggableRow provided={draggableProvided}
                                  snapshot={draggableSnapshot} portal={portal} content={{id: que.Id, username: que.Text}}/>
                                )}
                              </Draggable>
                              ))}
                              {droppableProvided.placeholder}
                            </Table>
                          </div>
                        )}
                      </Droppable>
                      <div className={styles.Created__Button}>
                        <Button
                          type="filled"
                          handleClicked={() => toggleEditMode('questions')}
                        >
                          Edit questions
                        </Button>
                      </div>
                    </div>
                  </div>
                  <div className={styles.Created__Start}>
                    <div className={styles.Created__Button}>
                      <Button type="filled" handleClicked={handleStartMatch}>Start</Button>
                    </div>
                  </div>
                </div>
              </div>
            </Panel>
            {editMode === 'questions' ? (
              <Panel>
                <div className={styles.Created__Edit__Questions}>
                  <h1>Questions</h1>
                  <Droppable droppableId="allQuestions">
                    {(droppableProvided) => (
                      <div ref={droppableProvided.innerRef} {...droppableProvided.droppableProps}>
                        <Table titles={['Id', 'Question']}>
                          {questions.map((queEl, index) => (
                            <Draggable
                              draggableId={queEl.Text}
                              index={index}
                              key={queEl.Id}
                            >
                              {(draggableProvided, draggableSnapshot) => (
                                <DraggableRow provided={draggableProvided}
                                snapshot={draggableSnapshot} portal={portal} content={{id: queEl.Id, username: queEl.Text}}/>
                              )}
                            </Draggable>
                          ))}
                          {droppableProvided.placeholder}
                        </Table>
                      </div>
                    )}
                  </Droppable>
                </div>
              </Panel>
            ) : null}
          </>
        );
        break;
      case 'Locked':
        matchContent = (
          <Panel>
            <div className={styles.MatchDetail}>
              <h1>Match nr {props.match.params.matchId}</h1>
              <p>Locked</p>
            </div>
          </Panel>
        );
        break;
      case 'Ended':
        matchContent = (
          <Panel>
            <div className={styles.MatchDetail}>
              <h1>Match nr {props.match.params.matchId}</h1>
              <p>Ended</p>
            </div>
          </Panel>
        );
        break;
      default:
        return <div>Invalid match status</div>;
    }
  }

  return (
    <div className={styles.Panels}>
      <DragDropContext onDragEnd={onDragEnd}>{matchContent}</DragDropContext>
    </div>
  );
};

export default MatchDetail;
