import { useEffect, useState } from 'react';

import axios from '../../../api/quiz';
import styles from './Questions.module.scss';
import Panel from '../../../components/Panel/Panel';
import Table from '../../../components/Table/Table';
import Loader from '../../../components/Loader/Loader';

const Questions = () => {
  const questionTitles = ['Id', 'Question'];
  const [questionList, setQuestionList] = useState<{id: number, text: string}[]>([]);

  useEffect(() => {
    axios.get('/question/')
    .then(({ data }) => {
      console.log(data.results);
      const fetchedQuestions = data.results.map((que: any) => ({
        id: que.Id,
        text: que.Text,
      }))
      setQuestionList(fetchedQuestions);
    })
    .catch(err => {
      console.log(err);
    })
  }, [])

  let questionElements = null;
  if (questionList.length > 0) {
    questionElements = questionList.map(questionEl => (
      <tr key={questionEl.id}>
        <td>{questionEl.id}</td>
        <td>{questionEl.text}</td>
      </tr>
    ))
  }

  return <Panel>
    <div className={styles.Questions}>
      <h1>Questions</h1>
      <div className={styles.Questions__List}>
        {questionList.length > 0 ? 
          <Table titles={questionTitles}>
            {questionElements}
          </Table>
        :
          <Loader />
        }
      </div>
    </div>
  </Panel>;
};

export default Questions;
