import { useRef } from "react";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import axios from "../../api/quiz";

import Panel from "../../components/Panel/Panel";
import Button from "../../components/Button/Button";
import styles from "./Register.module.scss";

const Register = () => {
  const { register, errors, handleSubmit, watch } = useForm({});
  const password = useRef({});
  password.current = watch("password", "");
  const onSubmit = async (data: any) => {
    const newUser = {
      username: data.username,
      password: data.password,
      email: data.email,
    };
    axios
      .post("/user/", newUser)
      .then((response) => {
        console.log(response);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Panel>
      <div className={styles.Register}>
        <h1>Create new account</h1>
        <div className={styles.Register__FormBox}>
          <form
            className={styles.Register__Form}
            onSubmit={handleSubmit(onSubmit)}
            noValidate
          >
            <div className={styles.Register__Form__Input}>
              <input
                id='username'
                name='username'
                type='text'
                placeholder='Username'
                autoComplete='username'
                ref={register({
                  required: "This is required",
                  minLength: {
                    value: 5,
                    message: "Minimum 5 characters",
                  },
                })}
              />
              <label htmlFor='username'>Username</label>
              <div
                className={
                  styles.Register__ErrorMessage +
                  " " +
                  (errors["username"] && styles.Register__ErrorMessage_show)
                }
              >
                {errors["username"] && errors["username"].message}
              </div>
            </div>
            <div className={styles.Register__Form__Input}>
              <input
                id='email'
                name='email'
                type='email'
                placeholder='E-mail'
                ref={register({
                  required: "This is required",
                  pattern: {
                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    message: "Invalid email address",
                  },
                })}
              />
              <label htmlFor='email'>E-mail</label>
              <div
                className={
                  styles.Register__ErrorMessage +
                  " " +
                  (errors["email"] && styles.Register__ErrorMessage_show)
                }
              >
                {errors["email"] && errors["email"].message}
              </div>
            </div>
            <div className={styles.Register__Form__Input}>
              <input
                id='password'
                name='password'
                type='password'
                placeholder='Password'
                autoComplete='new-password'
                ref={register({ required: "This is required" })}
              />
              <label htmlFor='password'>Password</label>
              <div
                className={
                  styles.Register__ErrorMessage +
                  " " +
                  (errors["password"] && styles.Register__ErrorMessage_show)
                }
              >
                {errors["password"] && errors["password"].message}
              </div>
            </div>
            <div className={styles.Register__Form__Input}>
              <input
                id='confirmpassword'
                name='confirmpassword'
                type='password'
                placeholder='Confirm password'
                autoComplete='new-password'
                ref={register({
                  required: "This is required",
                  validate: (value) =>
                    value === password.current || "The passwords do not match",
                })}
              />
              <label htmlFor='confirmpassword'>Confirm password</label>
              <div
                className={
                  styles.Register__ErrorMessage +
                  " " +
                  (errors["confirmpassword"] &&
                    styles.Register__ErrorMessage_show)
                }
              >
                {errors["confirmpassword"] && errors["confirmpassword"].message}
              </div>
            </div>
            <div className={styles.Register__Button}>
              <Button type='filled' isSubmit>Sign up</Button>
            </div>
          </form>
        </div>
        <div className={styles.Register__LoginInfo}>
          You have an account already? <Link to="/login">Log in</Link>
        </div>
      </div>
    </Panel>
  );
};

export default Register;
