import {
  AuthState,
  AuthActionTypes,
  AUTH_LOGIN_START,
  AUTH_LOGIN_SUCCESS,
  AUTH_LOGIN_FAIL,
  AUTH_LOGOUT,
  AUTH_NEW_TOKEN,
  AUTH_NEW_USER_DATA
} from './types';

const initialState: AuthState = {
  loggedIn: false,
  loading: false,
  token: '',
  refreshToken: '',
  tokenExpiration: 0,
  user: {},
  error: null
};

const authReducer = (state = initialState, action: AuthActionTypes): AuthState => {
  switch (action.type) {
    case AUTH_LOGIN_START:
      return { ...state, loading: true, error: null };
    case AUTH_LOGIN_SUCCESS:
      return { ...state, token: action.token, refreshToken: action.refreshToken, tokenExpiration: action.tokenExpiration, loggedIn: true, user: action.user, loading: false };
    case AUTH_LOGIN_FAIL:
      return { ...state, loading: false, error: action.error};
    case AUTH_LOGOUT:
      return { ...state, loggedIn: false, loading: false, token: '', user: {}, error: null };
    case AUTH_NEW_TOKEN:
      return { ...state, token: action.newToken, tokenExpiration: action.newTokenExpiration };
    case AUTH_NEW_USER_DATA:
      return { ...state, user: {...state.user, ...action.userData} }
    default:
      return state;
  }
}

export default authReducer;