import { Link, Redirect } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { connect, ConnectedProps } from 'react-redux';

import styles from './Login.module.scss';
import { AppState } from '../../store/store';
import { authLogin } from '../../store/auth/actions';
import { AuthData } from '../../store/auth/types';

import Panel from '../../components/Panel/Panel';
import Button from '../../components/Button/Button';
import Modal from '../../components/Modal/Modal';

const mapStateToProps = (state: AppState) => ({
  isLogged: state.auth.loggedIn,
  authError: state.auth.error
});

const mapDispatchToProps = (dispatch: any) => ({
  onLogin: (authData: AuthData) => dispatch(authLogin(authData)),
});

const connector = connect(mapStateToProps, mapDispatchToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
type LoginProps = PropsFromRedux;

const Login = ({ onLogin, isLogged, authError }: LoginProps) => {
  const { register, errors, handleSubmit } = useForm({});

  const onSubmit = async (data: any) => {
    const userData = {
      email: data.email,
      password: data.password,
    };
    // const basicToken = btoa(userData.username + ":" + userData.password);
    onLogin(userData);
  };

  return (
    <Panel>
      {authError ? <Modal type="error" content="Invalid credentials!" /> : null}
      {isLogged ? <Redirect to="/" /> : null}
      <div className={styles.Login}>
        <h1>Login</h1>
        <div className={styles.Login__FormBox}>
          <form
            className={styles.Login__Form}
            onSubmit={handleSubmit(onSubmit)}
            noValidate
          >
            <div className={styles.Login__Form__Input}>
              <input
                id="email"
                name="email"
                type="email"
                placeholder="E-mail"
                autoComplete="email"
                ref={register({
                  required: 'This is required',
                  pattern: {
                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    message: 'Invalid email address',
                  },
                })}
              />
              <label htmlFor="email">E-mail</label>
              <div
                className={
                  styles.Login__ErrorMessage +
                  ' ' +
                  (errors['email'] && styles.Login__ErrorMessage_show)
                }
              >
                {errors['email'] && errors['email'].message}
              </div>
            </div>
            <div className={styles.Login__Form__Input}>
              <input
                id="password"
                name="password"
                type="password"
                placeholder="Password"
                autoComplete="current-password"
                ref={register({ required: 'This is required' })}
              />
              <label htmlFor="password">Password</label>
              <div
                className={
                  styles.Login__ErrorMessage +
                  ' ' +
                  (errors['password'] && styles.Login__ErrorMessage_show)
                }
              >
                {errors['password'] && errors['password'].message}
              </div>
            </div>
            <div className={styles.Login__Button}>
              <Button type="filled" isSubmit>Sign in</Button>
            </div>
          </form>
        </div>
        <div className={styles.Login__RegisterInfo}>
          You don't have an account yet?
          <br />
          <Link to="/register">Create new account</Link>
        </div>
      </div>
    </Panel>
  );
};

export default connector(Login);
