import { NavLink, Link } from 'react-router-dom';
import { connect, ConnectedProps } from 'react-redux';

import { AppState } from '../../store/store';
import Button from '../Button/Button';
import styles from './Menu.module.scss';
import buttonStyles from '../Button/Button.module.scss';

const mapStateToProps = (state: AppState) => ({
  isLogged: state.auth.loggedIn,
  username: state.auth.user.username,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;

const Menu = ({ isLogged, username }: PropsFromRedux) => {
  return (
    <div className={styles.Menu}>
      {!isLogged ? (
        <NavLink
          to="/login"
          exact
          activeClassName={buttonStyles.Button__Transparent_Visited}
        >
          <div className={styles.Menu__Item}>
            <Button type="transparent">Login</Button>
          </div>
        </NavLink>
      ) : (
        <>
          <NavLink
            to="/leagues"
            exact
            activeClassName={buttonStyles.Button__Transparent_Visited}
          >
            <div className={styles.Menu__Item}>
              <Button type="transparent">Leagues</Button>
            </div>
          </NavLink>
          <NavLink
            to="/matches"
            exact
            activeClassName={buttonStyles.Button__Transparent_Visited}
          >
            <div className={styles.Menu__Item}>
              <Button type="transparent">Matches</Button>
            </div>
          </NavLink>
          <NavLink
            to="/add-question"
            exact
            activeClassName={buttonStyles.Button__Transparent_Visited}
          >
            <div className={styles.Menu__Item}>
              <Button type="transparent">Add Question</Button>
            </div>
          </NavLink>
          <div className={styles.Menu__Item}>
            <div className={styles.Menu__User}>
              <div className={styles.Menu__User__Name}>{username}</div>
              <div className={styles.Menu__User__Dropdown}>
                <Link to="/settings">
                  <div className={styles.Menu__User__Dropdown__Item}>
                    Settings
                  </div>
                </Link>
                <Link to="/logout">
                  <div className={styles.Menu__User__Dropdown__Item}>
                    Logout
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default connector(Menu);
