import { useEffect, useState } from "react";
import axios from "../../api/quiz";
import { Link } from "react-router-dom";

import Panel from "../../components/Panel/Panel";
import Button from "../../components/Button/Button";
import Loader from "../../components/Loader/Loader";

import styles from "./MainPage.module.scss";

const MainPage = () => {
  type rankingType = {
    rank: number;
    total_points: number;
    username: string;
  };
  const [ranking, setRanking] = useState<rankingType[]>([]);

  useEffect(() => {
    axios
      .get("/ranking/")
      .then(({ data }) => {
        setRanking(data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  let leaderboardList;
  if (ranking.length > 0) {
    leaderboardList = ranking.map((rankPerson, index) => {
      if(index < 10) {
        return (<li key={index + rankPerson.username} className={styles.Leaderboard}>
          <div className={styles.LeaderboardBox}>
            <div>{rankPerson.username}</div>
            <div>{rankPerson.total_points}</div>
          </div>
        </li>)
      }
      return null;
    });
  }

  return (
    <div className={styles.Main}>
      <Panel>
        <div className={styles.Main__Content}>
          <h1>Play with your friends!</h1>
          <p>
            Discover exciting quizes from all around the world and create your
            own! Participate in tournaments to become first in the leaderboard!
          </p>
          <Link to='/register'>
            <div className={styles.Main__Button}>
              <Button type='filled'>Create account</Button>
            </div>
          </Link>
        </div>
      </Panel>
      <Panel>
        <div className={styles.Main__Promo}>
          <h1>Leaderboard</h1>
          { !leaderboardList ? <Loader /> : <ol>{leaderboardList}</ol>}
        </div>
      </Panel>
    </div>
  );
};

export default MainPage;
