import { useState } from 'react';
import { useForm } from 'react-hook-form';

// import axios from '../../api/quiz';
import styles from './AddQuestion.module.scss';
import Panel from '../../components/Panel/Panel';
import Button from '../../components/Button/Button';
import Questions from './Questions/Questions';

const AddQuestion = () => {
  const [answerElements, setAnswerElements] = useState<number[]>([]);
  const { register, errors, handleSubmit } = useForm({});
  const onSubmit = async (data: any) => {
    console.log(data);
    //todo: wating for endpoint to post this data
  };

  const handleNewAnswer = () => {
    const newAnswers: number[] = [...answerElements];
    newAnswers.push(answerElements.length + 1);
    setAnswerElements(newAnswers);
  };

  const handleRemoveAnswer = () => {
    const newAnswers: number[] = [...answerElements];
    newAnswers.pop();
    setAnswerElements(newAnswers);
  };

  let additionalAnswers = null;
  if (answerElements.length > 0) {
    additionalAnswers = answerElements.map((answerId) => {
      const ansId = answerId + 2;
      const answerTag = `answer-${ansId}`;

      return (
        <div
          className={styles.AddQuestion__Form__Answer}
          key={`answer${ansId}`}
        >
          <input
            id={answerTag}
            name={answerTag}
            type="text"
            placeholder={`Answer ${ansId}`}
            ref={register({
              required: 'This is required',
            })}
          />
          <label
            htmlFor={answerTag}
            className={styles.AddQuestion__Form__Answer__Label}
          >{`Answer ${ansId}`}</label>
          <div
            className={
              styles.AddQuestion__ErrorMessage +
              ' ' +
              (errors[answerTag] && styles.AddQuestion__ErrorMessage_show)
            }
          >
            {errors[answerTag] && errors[answerTag].message}
          </div>
          <div className={styles.AddQuestion__Form__Answer__Radio}>
            <input
              type="checkbox"
              id={`correctAnswer-${ansId}`}
              name="correctAnswers"
              value={ansId}
              ref={register()}
            />
            <label htmlFor={`correctAnswer-${ansId}`}></label>
          </div>
        </div>
      );
    });
  }

  return (
    <div className={styles.Panels}>
      <Panel>
        <div className={styles.AddQuestion}>
          <h1>Add Question</h1>
          <form
            className={styles.AddQuestion__Form}
            onSubmit={handleSubmit(onSubmit)}
            noValidate
          >
            <div className={styles.AddQuestion__Form__Input}>
              <input
                id="question"
                name="question"
                type="text"
                placeholder="Question"
                autoComplete="question"
                ref={register({
                  required: 'This is required',
                })}
              />
              <label htmlFor="question">Question</label>
              <div
                className={
                  styles.AddQuestion__ErrorMessage +
                  ' ' +
                  (errors['question'] && styles.AddQuestion__ErrorMessage_show)
                }
              >
                {errors['question'] && errors['question'].message}
              </div>
            </div>
            <div className={styles.AddQuestion__AnswersLabel}>
              <div>Answers</div>
              <div>Correct?</div>
            </div>
            <div className={styles.AddQuestion__Form__Answer}>
              <input
                id="answer-1"
                name="answer-1"
                type="text"
                placeholder="Answer 1"
                ref={register({
                  required: 'This is required',
                })}
              />
              <label
                htmlFor="answer-1"
                className={styles.AddQuestion__Form__Answer__Label}
              >
                Answer 1
              </label>
              <div
                className={
                  styles.AddQuestion__ErrorMessage +
                  ' ' +
                  (errors['answer-1'] && styles.AddQuestion__ErrorMessage_show)
                }
              >
                {errors['answer-1'] && errors['answer-1'].message}
              </div>
              <div className={styles.AddQuestion__Form__Answer__Radio}>
                <input
                  type="checkbox"
                  id="correctAnswer-1"
                  name="correctAnswers"
                  value="1"
                  defaultChecked
                  ref={register()}
                />
                <label htmlFor="correctAnswer-1"></label>
              </div>
            </div>
            <div className={styles.AddQuestion__Form__Answer}>
              <input
                id="answer-2"
                name="answer-2"
                type="text"
                placeholder="Answer 2"
                ref={register({
                  required: 'This is required',
                })}
              />
              <label
                htmlFor="answer-2"
                className={styles.AddQuestion__Form__Answer__Label}
              >
                Answer 2
              </label>
              <div
                className={
                  styles.AddQuestion__ErrorMessage +
                  ' ' +
                  (errors['answer-2'] && styles.AddQuestion__ErrorMessage_show)
                }
              >
                {errors['answer-2'] && errors['answer-2'].message}
              </div>
              <div className={styles.AddQuestion__Form__Answer__Radio}>
                <input
                  type="checkbox"
                  id="correctAnswer-2"
                  name="correctAnswers"
                  value="2"
                  ref={register()}
                />
                <label htmlFor="correctAnswer-2"></label>
              </div>
            </div>
            {additionalAnswers}
            <div className={styles.AddQuestion__Control}>
              <div className={styles.AddQuestion__Control__Button}>
                <Button type="filled" handleClicked={handleNewAnswer}>
                  Add new answer
                </Button>
              </div>
              <div className={styles.AddQuestion__Control__Button}>
                <Button type="filled" handleClicked={handleRemoveAnswer}>
                  Remove answer
                </Button>
              </div>
            </div>
            <div className={styles.AddQuestion__Button}>
              <Button type="filled" isSubmit>
                Add question
              </Button>
            </div>
          </form>
        </div>
      </Panel>
      <Questions />
    </div>
  );
};

export default AddQuestion;
