import React from 'react';

import styles from './Button.module.scss';

type ButtonProps = { 
  children: React.ReactNode,
  type: 'transparent' | 'filled',
  handleClicked?: () => void
  isSubmit?: boolean;
}

const Button = ({ children, handleClicked, type, isSubmit }: ButtonProps) => {
  let buttonClasses = [styles.Button];
  if(type === 'transparent') {
    buttonClasses.push(styles.Button__Transparent);
  } else if(type === 'filled') {
    buttonClasses.push(styles.Button__Filled);
  }

  return (
    <button className={buttonClasses.join(' ')} onClick={handleClicked} type={isSubmit ? 'submit': 'button'}>{children}</button>
  )
}

export default Button
