import React, { ChangeEvent, useEffect, useState } from "react";
import Pagination from '@material-ui/lab/Pagination';

import axios from "../../api/quiz";
import Panel from "../../components/Panel/Panel";
import Button from "../../components/Button/Button";
import Loader from "../../components/Loader/Loader";
import styles from "./Leagues.module.scss";

const Leagues = () => {
  const [leaguesList, setLeaguesList] = useState([]);
  const [noLeagues, setNoLeagues] = useState(0);
  const [page, setPage] = useState(1);

  useEffect(() => {
    axios
      .get("/league/")
      .then((response) => {
        setNoLeagues(response.data.count);
        setLeaguesList(response.data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleChangePage = (event: ChangeEvent<unknown>, value: number): void => {
    axios
      .get(`/league/?page=${value}`)
      .then((response) => {
        setLeaguesList(response.data.results);
        setPage(value);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <Panel>
      <div className={styles.Leagues}>
        <h1>Leagues</h1>
        <div className={styles.Leagues__List}>
          <ul>
            {leaguesList.length > 0 ? leaguesList.map((leag: any) => <li key={leag.Id}>{leag.Name}</li>) : <Loader />}
          </ul>
        </div>
        <div className={styles.Pagination}>
          <Pagination count={Math.ceil(noLeagues/10)} color="primary" size="large" page={page} onChange={handleChangePage}/>
        </div>
        <div className={styles.Leagues__Button}>
          <Button type="filled">Add new League</Button>
        </div>
      </div>
    </Panel>
  );
};

export default Leagues;
