import { useEffect } from "react";
import { Route } from "react-router-dom";
import { spring, AnimatedSwitch } from "react-router-transition";
import { connect, ConnectedProps } from "react-redux";

import { authCheckState } from "./store/auth/actions";
import "./App.css";
import Layout from "./hoc/Layout/Layout";
import MainPage from "./pages/MainPage/MainPage";
import Register from "./pages/Register/Register";
import Login from "./pages/Login/Login";
import Logout from "./pages/Logout/Logout";
import Matches from "./pages/Matches/Matches";
import MatchDetail from "./pages/Matches/MatchDetail/MatchDetail";
import Leagues from "./pages/Leagues/Leagues";
import Settings from "./pages/Settings/Settings";
import AddQuestion from "./pages/AddQuestion/AddQuestion";

function mapStyles(styles: { opacity: number; scale: number }) {
  return {
    opacity: styles.opacity,
    transform: `scale(${styles.scale})`,
  };
}

function bounce(val: number) {
  return spring(val, {
    stiffness: 330,
    damping: 22,
  });
}

const bounceTransition = {
  atEnter: {
    opacity: 0,
    scale: 1.2,
  },
  atLeave: {
    opacity: bounce(0),
    scale: bounce(0.8),
  },
  atActive: {
    opacity: bounce(1),
    scale: bounce(1),
  },
};


type PropsFromRedux = ConnectedProps<typeof connector>;

const App = ({ onCheckAuth }: PropsFromRedux) => {
  useEffect(() => {
    onCheckAuth();
  }, [onCheckAuth]);
  
  return (
    <Layout>
      <AnimatedSwitch
        atEnter={bounceTransition.atEnter}
        atLeave={bounceTransition.atLeave}
        atActive={bounceTransition.atActive}
        mapStyles={mapStyles}
        className='switch-wrapper'
      >
        <Route path='/register' exact component={Register} />
        <Route path='/login' exact component={Login} />
        <Route path='/logout' exact component={Logout} />
        <Route path='/matches' exact component={Matches} />
        <Route path='/matches/:matchId' exact component={MatchDetail} />
        <Route path='/leagues' exact component={Leagues} />
        <Route path='/settings' exact component={Settings} />
        <Route path='/add-question' exact component={AddQuestion} />
        <Route path='/' exact component={MainPage} />
      </AnimatedSwitch>
    </Layout>
  );
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    onCheckAuth: () => dispatch(authCheckState())
  }
}

const connector = connect(null, mapDispatchToProps);

export default connector(App);