export interface AuthState {
  loggedIn: boolean;
  token: string;
  refreshToken: string;
  tokenExpiration: number;
  user: User;
  loading: boolean;
  error: Error | null;
}

export interface User {
  id?: number;
  username?: string;
  email?: string;
  first_name?: string;
  last_name?: string;
} 

export interface AuthData {
  email: string;
  password: string;
}

export const AUTH_LOGIN_START = "AUTH_LOGIN_START";
export const AUTH_LOGIN_SUCCESS = "AUTH_LOGIN_SUCCESS";
export const AUTH_LOGIN_FAIL = "AUTH_LOGIN_FAIL";

export const AUTH_LOGOUT = "AUTH_LOGOUT";

export const AUTH_NEW_TOKEN = "AUTH_NEW_TOKEN";
export const AUTH_NEW_USER_DATA = "AUTH_NEW_USER_DATA";

interface LoginStartAction {
  type: typeof AUTH_LOGIN_START;
}

interface LoginSuccessAction {
  type: typeof AUTH_LOGIN_SUCCESS;
  user: User;
  token: string;
  refreshToken: string;
  tokenExpiration: number;
}

interface LoginFailAction {
  type: typeof AUTH_LOGIN_FAIL;
  error: Error;
}

interface LogoutAction {
  type: typeof AUTH_LOGOUT;
}

interface NewTokenAction {
  type: typeof AUTH_NEW_TOKEN;
  newToken: string;
  newTokenExpiration: number;
}

interface NewUserData {
  type: typeof AUTH_NEW_USER_DATA;
  userData: User;
}

export type AuthActionTypes = 
  LoginStartAction | LoginSuccessAction | LoginFailAction | LogoutAction | NewTokenAction | NewUserData;
